socialIconsContainer {
  background: #0A122A;
  margin: 0;
  padding: 0; }

/* Let's style the wrapper in such away that,
it should always be at the center of the browser */
.socialIconsWrapper {
  position: relative;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 550px; }

/* Style the Un-order list by setting its list-style to none */
.socialIconsWrapper ul {
  list-style: none; }

/* Style the list items inside the UL list, by setting its width, height and line-height
  and float them to left and set its border and border-radius.
 */
.socialIconsWrapper ul li {
  width: 75px;
  height: 75px;
  line-height: 75px;
  margin: 0 10px;
  text-align: center;
  cursor: pointer;
  border-radius: 50%;
  border: 5px solid #D8E2DC;
  float: left;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease; }

/* Style the icons by setting its color and margin-top value to 20px
to align it properly */
.socialIconsWrapper ul li .fa {
  color: #D8E2DC;
  margin-top: 20px;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease; }

/* Now target the specific li classes for styling and use box-shadow effect to border and text-shadow effect
  to icons for glowing effect and use transition property for smooth transition effect. */
/*facebook*/
.socialIconsWrapper a {
  color: inherit; }

.socialIconsWrapper ul li:hover.facebook {
  border: 5px solid #3b5998;
  -webkit-box-shadow: 0 0 15px #3b5998;
          box-shadow: 0 0 15px #3b5998;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease; }

.socialIconsWrapper ul li:hover .fa-facebook {
  color: #3b5998;
  text-shadow: 0 0 15px #3b5998;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease; }

/*twitter*/
.socialIconsWrapper ul li:hover.twitter {
  border: 5px solid #00aced;
  -webkit-box-shadow: 0 0 15px #00aced;
          box-shadow: 0 0 15px #00aced;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease; }

.socialIconsWrapper ul li:hover .fa-twitter {
  color: #00aced;
  text-shadow: 0 0 15px #00aced;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease; }

/* instagram */
.socialIconsWrapper ul li:hover.instagram {
  border: 5px solid #bc2a8d;
  -webkit-box-shadow: 0 0 15px #bc2a8d;
          box-shadow: 0 0 15px #bc2a8d;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease; }

.socialIconsWrapper ul li:hover .fa-instagram {
  color: #bc2a8d;
  text-shadow: 0 0 15px #bc2a8d;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease; }

/* google */
.socialIconsWrapper ul li:hover.google {
  border: 5px solid #dd4b39;
  -webkit-box-shadow: 0 0 15px #dd4b39;
          box-shadow: 0 0 15px #dd4b39;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease; }

.socialIconsWrapper ul li:hover .fa-google {
  color: #dd4b39;
  text-shadow: 0 0 15px #dd4b39;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease; }

/* whatsapp */
.socialIconsWrapper ul li:hover.whatsapp {
  border: 5px solid #4dc247;
  -webkit-box-shadow: 0 0 15px #4dc247;
          box-shadow: 0 0 15px #4dc247;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease; }

.socialIconsWrapper ul li:hover .fa-whatsapp {
  color: #4dc247;
  text-shadow: 0 0 15px #4dc247;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease; }

/* media queries */
@media screen and (max-width: 640px) {
  .socialIconsWrapper {
    width: 350px; }
  .socialIconsWrapper ul li {
    margin-top: 10px; }
  .socialIconsWrapper ul li.google {
    margin-left: 60px; } }

@media screen and (max-width: 340px) {
  .socialIconsWrapper {
    width: 150px; }
  .socialIconsWrapper ul li {
    margin: 15px; }
  .socialIconsWrapper ul li.google {
    margin-left: 15px; } }



