@charset "UTF-8";
.quoteWrapper {
  color: #fff;
  padding-top: 50px;
  margin-bottom: 500px; }

.quoteContainer {
  width: 590px;
  margin: 140px auto;
  position: relative; }

.quoteWrapper blockquote {
  width: 565px;
  font: 2.65em CallunaRegular;
  letter-spacing: .075em;
  float: left;
  padding: 45px 0 30px 25px;
  margin: 0;
  border-top: 2px dotted #858585;
  border-bottom: 2px dotted #858585;
  -webkit-transform: rotate(-3deg);
  -moz-transform: rotate(-3deg); }

.quoteContainer:before {
  content: "“";
  font-size: 13em;
  position: absolute;
  left: -100px;
  color: #666; }

.quoteContainer:after {
  content: "”";
  font-size: 13em;
  position: absolute;
  right: -100px;
  top: 150px;
  color: #666; }

.quoteWrapper blockquote:first-line {
  font: 1.85em "Sketch Block";
  color: #fff79e; }

.quoteWrapper blockquote:first-letter {
  font-size: 2.9em;
  text-transform: lowercase;
  float: left;
  line-height: .52em;
  margin-right: -18px;
  position: relative;
  z-index: 1; }

.quoteWrapper blockquote strong:first-child {
  margin-left: 8px;
  letter-spacing: 0; }

.quoteWrapper blockquote strong:last-child {
  font: 2em "Museo 700";
  text-transform: uppercase;
  letter-spacing: 0; }

.quoteWrapper blockquote em {
  border-bottom: 2px dotted #858585; }

.quoteWrapper blockquote + b {
  float: right;
  margin-top: 10px;
  font: 1.6em CallunaRegular;
  letter-spacing: .15em; }

.quoteWrapper blockquote + b:first-letter {
  color: #fff79e;
  font-size: 1.3em;
  font-style: italic;
  letter-spacing: .25em; }

@-moz-document url-prefix() {
  blockquote:first-letter {
    margin-top: -29px; } }



